import axiosInstance from './axios';
import axios from 'axios';

// axios
const directAxios = axios.create({
  baseURL: 'http://124.70.74.246:8081',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true, // 
});

//  - 
directAxios.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

interface LoginData {
  username: string;
  password: string;
}

interface RegisterData {
  username: string;
  password: string;
  email: string;
  name: string;
  gender?: string;
  role?: string;
}

interface ForgotPasswordData {
  email: string;
}

interface SendVerificationCodeData {
  email: string;
}

interface VerifyUsernameEmailData {
  username: string;
  email: string;
}

interface ResetPasswordWithCodeData {
  username: string;
  email: string;
  code: string;
  newPassword: string;
}

interface ResetPasswordData {
  token: string;
  password: string;
}

interface AuthResponse {
  token: string;
  user: {
    id: number;
    username: string;
    name: string;
    email: string;
    role: string;
    gender?: string;
    phone?: string;
  };
}

// 
interface LoginResponse {
  token?: string;
  type?: string;
  id: number;
  username: string;
  name: string;
  email: string;
  gender: string;
  phone: string;
  roles: string[];
}

/**
 * 
 * @param data 
 * @returns Promise
 */
export const login = async (data: LoginData): Promise<AuthResponse> => {
  try {
    console.log(':', data.username);
    // API
    const response = await directAxios.post('/api/auth/signin', data);
    console.log(':', response);
    
    // 
    const responseData = response.data;
    
    // tokentoken
    // token
    const tempToken = 'temp_token_' + Math.random().toString(36).substring(2);
    
    // 
    const authResponse: AuthResponse = {
      token: responseData.token || tempToken, // tokentoken
      user: {
        id: responseData.id,
        username: responseData.username,
        name: responseData.name || responseData.username,
        email: responseData.email || '',
        role: Array.isArray(responseData.roles) && responseData.roles.length > 0 
              ? responseData.roles[0] 
              : 'ROLE_USER',
        gender: responseData.gender,
        phone: responseData.phone
      }
    };
    
    console.log(':', authResponse);
    return authResponse;
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

