/**
 * 
 * @param data 
 * @returns Promise
 */
export const updateProfile = async (data: any) => {
  // ID
  const userId = JSON.parse(localStorage.getItem('user') || '{}').id || data.id;
  
  // gender
  const requestData = {
    ...data,
    id: userId,
  };
  
  console.log(':', requestData);
  
  return await directAxios.put(`/api/users/${userId}`, requestData);
};

