import React, { createContext, useContext, useState, useEffect } from 'react';
import { login as apiLogin, register as apiRegister, logout as apiLogout } from '../api/auth';

// 
export interface User {
  id: number;
  username: string;
  name: string;
  email: string;
  role: string;
  classId?: number;
  gender?: string;
  phone?: string;
}

// 
interface AuthContextType {
  user: User | null;
  token: string | null;
  isAuthenticated: boolean;
  loading: boolean;
  error: string | null;
  login: (username: string, password: string) => Promise<void>;
  register: (userData: any) => Promise<any>;
  logout: () => void;
  clearError: () => void;
}

// 
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// 
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  //  - 
  useEffect(() => {
    const loadUserFromStorage = () => {
      try {
        const storedToken = localStorage.getItem('token');
        const storedUser = localStorage.getItem('user');
        
        // token
        if (storedToken && storedToken !== 'undefined' && storedToken !== 'null' && storedUser) {
          try {
            // 
            const userObj = JSON.parse(storedUser);
            if (userObj && userObj.id) {
              setToken(storedToken);
              setUser(userObj);
            } else {
              // 
              localStorage.removeItem('token');
              localStorage.removeItem('user');
              // 
              tryAutoLogin();
            }
          } catch (e) {
            // JSON
            localStorage.removeItem('token');
            localStorage.removeItem('user');
            // 
            tryAutoLogin();
          }
        } else {
          // token
          if (storedToken === 'undefined' || storedToken === 'null') {
            localStorage.removeItem('token');
            localStorage.removeItem('user');
          }
          // 
          tryAutoLogin();
        }
      } catch (err) {
        console.error(':', err);
        // 
        localStorage.removeItem('token');
        localStorage.removeItem('user');
      } finally {
        setLoading(false);
      }
    };
    
    loadUserFromStorage();
  }, []);

  // 
  const tryAutoLogin = async () => {
    // 
    const currentPath = window.location.pathname;
    
    // 
    const skipAutoLoginPaths = ['/login', '/register', '/forgot-password', '/reset-password'];
    const shouldSkipAutoLogin = skipAutoLoginPaths.some(path => 
      currentPath === path || currentPath === path + '/'
    );
    
    if (shouldSkipAutoLogin) {
      console.log('/');
      setLoading(false);
      return;
    }
    
    // 
    console.log('');
    setLoading(false);
  };

  // 
  const handleLogin = async (username: string, password: string) => {
    setLoading(true);
    setError(null);
    
    try {
      console.log(':', username);
      const response = await apiLogin({ username, password });
      console.log('API:', { 
        hasToken: !!response.token, 
        tokenLength: response.token ? response.token.length : 0,
        userId: response.user.id,
        role: response.user.role,
        gender: response.user.gender,  // 
        phone: response.user.phone  // 
      });
      
      // token
      if (!response.token || response.token === 'undefined' || response.token === 'null') {
        console.error(': ');
        throw new Error('');
      }
      
      // 
      localStorage.setItem('token', response.token);
      localStorage.setItem('user', JSON.stringify(response.user));
      
      // 
      setToken(response.token);
      setUser(response.user);
      
      console.log('');
      
      // 
      return Promise.resolve();
    } catch (err: any) {
      console.error(':', err);
      setError(err.response?.data?.message || err.message || '');
      return Promise.reject(err);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleRegister = async (userData: any) => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await apiRegister(userData);
      console.log(':', response.data);
      
      // 
      return Promise.resolve(response);
    } catch (err: any) {
      console.error(':', err);
      setError(err.response?.data?.message || '');
      return Promise.reject(err);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleLogout = async () => {
    console.log('');
    
    try {
      // API
      await apiLogout();
      console.log('API');
    } catch (error) {
      console.error('API:', error);
    } finally {
      // 
      setToken(null);
      setUser(null);
      
      // 
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      
      // 
      localStorage.removeItem('rememberedUsername');
      localStorage.removeItem('rememberedPassword');
      
      console.log('');
    }
  };

  // 
  const clearError = () => {
    setError(null);
  };

  // 
  const contextValue = {
    user,
    token,
    isAuthenticated: !!token,
    loading,
    error,
    login: handleLogin,
    register: handleRegister,
    logout: handleLogout,
    clearError
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
};

// 
export const useAuth = () => {
  const context = useContext(AuthContext);
  
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  
  return context;
};

export default AuthContext; 